* {
  font-family: Electrolize, sans-serif;
}

body {
  text-align: center;
  background-color: #f2f2f2;
}

h3 {
  margin-top: 100px;
}

.img {
  max-width: 100%;
}

.container {
  width: 280px;
  background-color: gray;
  border-radius: 5px;
  margin: 5px auto 50px;
  padding: 20px 0;
}

.result {
  text-align: right;
  width: 250px;
  box-sizing: border-box;
  background-color: #f2f2f2;
  border: none;
  border-radius: 5px;
  margin-bottom: 5px;
  padding: 10px;
  font-size: 24px;
}

.btn {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #f2f2f2;
  border: none;
  border-radius: 5px;
  outline: none;
  margin: 5px;
  font-size: 24px;
  transition: background-color .3s ease-in-out;
}

.btn:hover, .btn:focus {
  background-color: #ccc;
}

.btn.operator {
  background-color: #e6e6e6;
}

.btn.operator:hover {
  background-color: #bfbfbf;
}

.btn.operator:focus {
  background-color: #bfbfbf4d;
}

.btn.equals {
  color: #fff;
  background-color: #00b894;
}

.btn.equals:hover, .btn.equals:focus {
  background-color: #00a887;
}

